.text-bold {
  font-weight: bold;
}
.text-muted {
  color: #a0abba;
}
.text-secondary {
  color: #888;
  font-size: 1rem;
}
.text-danger {
  color: #dd4b39;
}
.text-primary {
  color: #42A5F5;
}
.text-white {
  color: #F9FAFC;
}
.text-large {
  font-size: 1.1rem;
  font-weight: normal;
}
.text-small {
  font-size: .9rem;
  font-weight: normal;
}
.text-tiny {
  font-size: .8rem;
  font-weight: normal;
}
.text-mini {
  font-size: .7rem;
  font-weight: normal;
}
.link {
  font-size: 13px;
  padding: .1em .2em;
  border-radius: .2em;
  text-decoration: none;
  cursor: pointer;
  background-color: transparent;
  border: none;
  box-shadow: none;
  font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
}
.link:focus,
.link:hover,
.link:active {
  text-decoration: none;
}
.link-default {
  color: #666;
}
.link-default:focus,
.link-default:hover,
.link-default:active {
  color: #fff;
  background-color: #666;
}
.link-primary {
  color: #42A5F5;
}
.link-primary:focus,
.link-primary:hover,
.link-primary:active {
  color: #fff;
  background-color: #42A5F5;
}
.link-danger {
  color: #dd4b39;
}
.link-danger:focus,
.link-danger:hover,
.link-danger:active {
  color: #fff;
  background-color: #dd4b39;
}
.link-white {
  color: rgba(255, 255, 255, 0.8);
}
.link-white:focus,
.link-white:hover,
.link-white:active {
  color: #fff;
  transition: color .3s linear;
}
.link.loading.button:before {
  border-color: rgba(0, 0, 0, 0.1);
}
.link.loading:after {
  border-top-color: #767676;
}
.link.loading.loading.loading.loading.loading {
  position: relative;
  cursor: default;
  text-shadow: none!important;
  color: transparent!important;
  opacity: 1;
  pointer-events: auto;
  transition: all 0s linear,opacity .1s ease;
}
.link.loading:before {
  position: absolute;
  content: '';
  top: 50%;
  left: 50%;
  margin: -0.64285714em 0 0 -0.64285714em;
  width: 1em;
  height: 1em;
  border-radius: 500rem;
  border: 0.2em solid rgba(0, 0, 0, 0.15);
}
.link.loading:after {
  position: absolute;
  content: '';
  top: 50%;
  left: 50%;
  margin: -0.64285714em 0 0 -0.64285714em;
  width: 1em;
  height: 1em;
  animation: loader .6s linear;
  animation-iteration-count: infinite;
  border-radius: 500rem;
  border-color: #fff transparent transparent;
  border-style: solid;
  border-width: .2em;
  box-shadow: 0 0 0 1px transparent;
}
.text-center {
  text-align: center;
}
.pull-right {
  float: right;
}
.hidden {
  display: none;
}
.ui.label.fade-label,
.fade-label {
  background-color: #f5f5f5 !important;
  font-size: .9rem;
  padding: .3em .4em;
  border-radius: 0;
  margin-left: 0;
}
.pill-filter {
  padding: 0 2rem;
  margin: 1rem 0;
}
.pill-filter .filter-label {
  margin-bottom: .3rem;
  margin-right: 1rem;
  border: solid 1px transparent;
  border-radius: 2px;
  padding: 2px 3px;
  display: inline-block;
  cursor: pointer;
  font-size: .9em;
  color: #666;
}
.pill-filter .filter-label.active {
  background-color: #E3F2FD;
  border: solid 1px #90CAF9;
  pointer-events: none;
}
.pill-filter .filter-label .action-icon {
  padding-left: .2em;
  font-size: .8em;
  display: inline-block;
  color: #dd4b39;
  vertical-align: top;
  cursor: pointer;
  pointer-events: auto;
}
.pill-filter .filter-label .action-icon svg {
  margin: 0;
}
@-webkit-keyframes loader {
  from {
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }
  to {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes loader {
  from {
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }
  to {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
.header.header-lb {
  border-left: 0.3rem solid #42A5F5;
  padding-left: .5rem;
  font-weight: normal;
  line-height: 1;
  margin-top: .5rem;
  margin-bottom: .5rem;
}
.pad.button {
  display: inline-block;
  border: none;
  cursor: pointer;
  padding: .2em .4em;
  border-radius: .2em;
  vertical-align: middle;
  box-sizing: border-box;
  margin: 0 .5em 0 0;
  font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
  line-height: 1.5em;
  background-color: transparent;
}
.pad.button:hover,
.pad.button:active {
  background-color: rgba(153, 153, 153, 0.2);
}
.pad.button:focus {
  outline: none;
}
.pad.button.disabled {
  opacity: .5 !important;
  pointer-events: none;
}
.pad.button.primary {
  background-color: #42A5F5;
  color: #fff;
}
.pad.button.primary:hover,
.pad.button.primary:active {
  background-color: #2f9cf4;
}
.fa-icon {
  height: 1em;
  width: auto;
  vertical-align: middle;
  margin-right: .5em;
}
.pad.button .fa-icon {
  display: block;
  margin: auto;
}
.pad.button.circle {
  height: 1.8em;
  width: 1.8em;
  border-radius: 100%;
}
.dropdown .fa-icon {
  margin: auto 0 auto .4em;
}
.swal-button {
  background-color: transparent;
  color: rgba(0, 0, 0, 0.6);
  box-shadow: 0 0 0 1px rgba(34, 36, 38, 0.15) inset;
  padding: .4em 1.2em;
  border-radius: .3em;
  font-size: 14px;
}
.swal-button:hover,
.swal-button:active {
  box-shadow: 0 0 0 1px rgba(34, 36, 38, 0.35) inset, 0 0 0 0 rgba(34, 36, 38, 0.15) inset;
  color: rgba(0, 0, 0, 0.8);
}
.swal-button:focus {
  outline: none;
  box-shadow: 0 0 0 1px #fff, 0 0 0 2px rgba(43, 114, 165, 0.29);
}
.swal-button--confirm {
  color: white;
  box-shadow: none;
  background-color: #42A5F5;
}
.swal-button.swal-button--confirm:hover,
.swal-button.swal-button--confirm:active {
  color: white;
  box-shadow: none;
  background-color: #028bf9;
}
.swal-overlay {
  background-color: rgba(0, 0, 0, 0.5);
}
.swal-title {
  font-size: 24px;
}
.swal-footer {
  background-color: #f5f8fa;
  margin-top: 32px;
  border-top: 1px solid #E9EEF1;
  overflow: hidden;
}
.flatpickr-calendar span.flatpickr-weekday {
  color: white;
}
.flatpickr-day.today {
  border-color: #ddd;
}
.content-empty {
  margin-top: 6rem;
  text-align: center;
}
.content-empty img {
  max-width: 100px;
}
.content-empty p {
  color: #888;
}
.pad-state.label {
  background-color: transparent;
  vertical-align: middle;
  display: inline;
  align-items: center;
  font-size: .9rem;
  opacity: .8;
  transition: opacity .3s linear;
}
.pad-state.label .icon {
  font-size: .6rem;
}
.pad-state.label.red {
  color: #dd4b39;
}
.pad-state.label.teal {
  color: #00b5ad;
}
.pad-state.label.orange {
  color: #f2711c;
}
.pad-state.label.grey {
  color: #9E9E9E;
}
.pad-state.label.blue,
.pad-state.label.primary {
