}
.pad.ui.table {
  border-radius: 0;
  border-left: none;
  border-right: none;
}
.pad.ui.table td,
.pad.ui.table th {
  padding: .8em 1em;
}
.pad.ui.table .content-row {
  cursor: pointer;
}
.pad.ui.table .content-row .row-center-title {
  font-weight: bold;
}
.pad.ui.table .content-row:hover,
.pad.ui.table .content-row:active,
.pad.ui.table .content-row:focus {
  background-color: #f6faff;
}
.pad.ui.table .content-row:hover .row-center-title,
.pad.ui.table .content-row:active .row-center-title,
.pad.ui.table .content-row:focus .row-center-title {
