}
.code-label {
  font-size: .9em;
  padding: .1em .2em;
  border-radius: .2em;
  background-color: #EFF2F7;
  font-size: #444;
  font-weight: normal;
  font-family: Lucida Console, Courier, monospace;
  margin-right: .8em;
}
.header-actions-bar {
  text-align: left;
  margin-bottom: 1rem;
}
.header-actions-bar.right.aligned {
  text-align: right;
}
.toasted-container .toasted.primary.success,
.toasted-container .toasted.bubble.success {
