}
:root {
  font-size: 14px;
}
body {
  font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
  color: #444;
  -webkit-font-smoothing: antialiased;
  margin: 0;
}
* {
  box-sizing: border-box;
}
[v-cloak] {
  display: none;
}
.app-content {
  margin-left: 13rem;
}



