@import "./base-variables.less";

.toasted-container {
  .toasted.primary.success, .toasted.bubble.success {
    background: @color-teal;
  }
  .toasted.primary.error, .toasted.bubble.error {
    background: @color-red;
  }
  .toasted.primary.info, .toasted.bubble.info {
    background: @color-blue;
  }

  .toasted.outline.success {
    color: @color-teal;
    border-color: @color-teal;
  }
  .toasted.outline.error {
    color: @color-red;
    border-color: @color-red;
  }
  .toasted.outline.info {
    color: @color-blue;
    border-color: @color-blue;
  }

  .toasted.primary {
    -webkit-font-smoothing: auto;
    border-radius: .4rem;
  }
  .toasted.bubble {
    -webkit-font-smoothing: auto;
  }
  .toasted.outline {
    font-weight: normal;
  }
}



